<template>
  <div class="home">
    <div class="main">
      <div class="title">
        <span class="title_col title_col1">序号</span>
        <span class="title_col title_col2">标题</span>
        <span class="title_col title_col3">负责人</span>
        <span class="title_col title_col3">开始时间</span>
        <span class="title_col title_col3">完成时间</span>
        <span class="title_col title_col3">用时</span>
      </div>
      <div class="content" v-for="(val,idx) in tableData" :key="idx">
        <div class="title_col title_col1">
          <input class="title_col_input" type="text" :value="idx+1">
        </div>
        <div class="title_col title_col1">
          <input class="title_col_input" type="text"  v-model="val.title">
        </div>
        <div class="title_col title_col1">
          <input class="title_col_input" type="text" v-model="val.charge">
        </div>
        <div class="title_col title_col1">
          <input class="title_col_input" type="text" v-model="val.startTime" placeholder="例2020-01-12">
        </div>
        <div class="title_col title_col1">
          <input class="title_col_input" type="text" v-model="val.endTime"  placeholder="例2020-01-12">
        </div>
        <div class="title_col title_col1">
          <input class="title_col_input" type="text" v-model="val.time" @input="inputTime(idx)"  placeholder="例0.5">
        </div>
      </div>
      <div class="add" @click="addInput">增加</div>
      <div class="countWrap">
        <div class="countWrap_time">时间总计:{{countTime}}</div>
        <!-- <div class="countWrap_email">
          <select class="countWrap_email_select">
            <option>qq</option>
            <option>网易</option>
