</template>

<script>
// const axios = require('axios');
// const api = "http://localhost:3000";

export default {
  name: 'home',
  data(){
    return {
        tableData: [
          {
            title:'',
            charge:'',
            startTime:'',
            endTime:'',
            time:'',
          }, 
        ],
        countTime:0,
    }
  },
  props: {
    
  },
  mounted(){
    // axios.get(api+'/getData')
    //   .then(function (res) {
    //     console.log(res);
    //   })
    //   .catch(function (err) {
    //     console.log(err);
    //   })
    // //post
    // axios.post(api+'/person', {
    //   firstName: 'frend',
    //   lastName: 'Flintstone'
    // })
    // .then(function (res) {
    //   console.log(res);
    // })
    // .catch(function (err) {
    //   console.log(err);
    // });
  },
  watch:{
    timeChange(newVal,oldVal){
      this.countTime = newVal;
    }
  },
  methods:{
    addInput:function(){
      var tableData = this.tableData;
      tableData.push({
        title:'',
        charge:'',
        startTime:'',
        endTime:'',
        time:'',
      })
    },
    inputTime:function(idx){
      var that = this;
      setTimeout(function(){
        that.tableData[idx].time =  that.tableData[idx].time.replace(/[^\d & ^\.]/g, '');
      },10);
    },
    downTxt:function(){
        var text = "";
        var data  = this.tableData;
        data.forEach(function(val,idx){
          var title = val.title.toString();
          var charge =  val.charge.toString();
          var startTime = val.startTime.toString();
          var endTime = val.endTime.toString();
          var time =  val.time.toString();
          if( title || charge || startTime || endTime || time){
            text += idx.toString()+"  "+title+"  "+charge+"  "+startTime+"  "+endTime+"  "+time + "\r\n";
          }
        });
        if(text){
          text += "时间总计"+this.countTime;
        }
        var element = document.createElement('a');
        element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
        element.setAttribute('download', 'worktxt.txt');
      
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    },
  },
  computed:{
    timeChange(){
      var data = this.tableData;
      var result = 0;

      data.forEach(val => {
        result+=(val.time-0);
      });
      return result;
    },
  }
}
</script>

